
.navBar {
  top: 0;
  left: 0;
  position: sticky;
  z-index: 1100;
  display: flex;
  display: flex;
  justify-content: space-between;
  align-items: center;
  min-height: 64px;
gap: 10px;
  padding-top: 12px;
  padding-bottom: 12px;
  padding-right: 24px;
  padding-left: 24px;
  background-color: skyblue;
  box-shadow: 0px 2px 4px -1px rgba(0, 0, 0, 0.2),
    0px 4px 5px 0px rgba(0, 0, 0, 0.14), 0px 1px 10px 0px rgba(0, 0, 0, 0.12);
}

.nav{
    display: flex;
    gap: 10px;
    text-decoration: none;
}



